/* Target elemen root dari komponen App Anda */
.App {
  text-align: left; /* Default alignment, bisa disesuaikan jika perlu */
  min-height: 100vh; /* Pastikan aplikasi setidaknya setinggi viewport */
  display: flex;
  flex-direction: column;
  background-color: #f0f2f5; /* Warna latar belakang netral untuk seluruh halaman di luar konten utama */
}


.app-header {
  background-color: #282c34;
  padding: 20px;
  color: white;
  text-align: center;
}

.app-footer {
  background-color: #333;
  padding: 15px;
  color: #ccc;
  text-align: center;
  margin-top: auto;
}



a {
  color: #007bff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}


/*
Konten utama Anda (ProductCatalog, ProductDetailPage) akan dirender di dalam .App.
Styling spesifik untuk konten tersebut sudah Anda tangani di file CSS masing-masing
(ProductCatalog.css, ProductDetailPage.css, ProductCard.css).

App.css ini lebih untuk 'kulit' terluar aplikasi Anda.
*/